import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
  function Square(props){
    return(
      <button className="square" onClick={props.onClick}>
          {props.value}
        </button>
      );
  }
  
  class Board extends React.Component {
    constructor(props){
      super(props);
      this.state = {
        squares: Array(9).fill(null),
        xIsNext: true,
        moves: 0,
      };
    }

    handleClick(i) {
      const squares = this.state.squares.slice();
      const winLine = calculateWinner(squares);
      const winner = this.state.squares[winLine[0]];
      if(winner || squares[i]){
        return;
      }
      squares[i] = this.state.xIsNext ? 'X' : 'O';
      this.setState({
        squares: squares,
        xIsNext: !this.state.xIsNext,
        moves: this.state.moves + 1,
      });
    }
    renderSquare(i) {
      return (
        <Square 
          value={this.state.squares[i]}
          onClick={() => this.handleClick(i)}
          />
      );
    }

  
    render() {
      const winLine = calculateWinner(this.state.squares);
      let status;
      const winner = this.state.squares[winLine[0]];
      
      if(winner == null && this.state.moves === 9){
        status = 'Draw!';
      }else if(winner){
        status = 'Winner: ' + winner + '!';
      }else {
        status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
      }
      
      return (
        <div>

          <div className="status">
            <button className='status' onClick={(e) => {
                for(let i =0; i<9; i++){
                  this.setState({
                    squares: Array(9).fill(null),
                    moves: 0,
                  })
                }
            }}>Reset</button>
            {status}
          </div>
          <div className="board-row">
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
          </div>
          <div className="board-row">
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
          </div>
          <div className="board-row">
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
          </div>
        </div>
      );
      
    }
  }
  
  class Game extends React.Component {
    render() {
      return (
        <div className="game">
          <div className="game-board">
            <Board />
          </div>
         
        </div>
      );
    }
  }

  class StartGameControl extends React.Component{
    constructor(props){
      super(props);
      this.handleClick = this.handleClick.bind(this);
      this.state = {hasBegun: false};
    }

    handleClick(){
      this.setState({hasBegun: true});
    }

    render(){
      const hasBegun = this.state.hasBegun;
      if(hasBegun){
        return <Game />;
      } else {
        return (
          <div className="status">
            TicTacToe!
            <button className='status' onClick={this.handleClick}>Start</button>
            
          </div>
        );
      }
    }
  }
  
  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        const winLine = [a, b, c];
        console.log(winLine);
        return winLine;
      }
    }
    return 0;
  }
  
  
  const root = ReactDOM.createRoot(document.getElementById("root"));
  root.render(<StartGameControl />);
  